    }

    .hintBox__hint {
        position: absolute;
        width: 312px;
        height: -webkit-max-content;
        height: max-content;
        /* top: 0;
        left: 0; */
        font-family: 'Indie Flower', cursive;
        font-size: 24px;
        transition: .2s;
        transition-timing-function: ease-in-out;
        transition-delay: .1s;
        cursor: default;
        -webkit-animation: shaker 5s linear 0s infinite alternate;
                animation: shaker 5s linear 0s infinite alternate;
        display: flex;
        align-items: center;
        text-indent: -20px;
