    .textBox__isometric {
        /* height: 200px; */
        margin-top: 90px;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
    }

    .textBox__isometric a {
        position: relative;
        display: block;
        width: 80px;
        height: 100px;
        /* transform: perspective(1000px) rotateX(15deg) skewY(-15deg); */
        text-decoration: none;
        color: #242424;
    }

    .textBox__isometric a:hover .isometric__content {
        -webkit-transform: translate(-25px, -35px);
                transform: translate(-25px, -35px);
        transition: .3s;
    }

    .isometric__content {
        position: relative;
        z-index: 1;
        width: 100%;
        height: 100%;
        background-color: #fff;
        font-size: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
        transition: .3s;
    }

    .isometric__link {
        margin: 0 25px;
    }

    .isometric__linkName {
        position: relative;
        z-index: 0;
        color: var(--pseudoColor);
        opacity: 1;
        text-align: center;
        font-size: 36px;
        -webkit-transform: skewX(45deg) translate(65px, -70px);
                transform: skewX(45deg) translate(65px, -70px);
        letter-spacing: 0.03em;
    }

    .isometric__content::before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        height: 100%;
        width: 20px;
        background: var(--pseudoColor);
        -webkit-transform: skewY(45deg) translate(-20px, 10px);
                transform: skewY(45deg) translate(-20px, 10px);
    }

    .isometric__content::after {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        height: 20px;
        width: 100%;
        background: var(--pseudoColor);
        -webkit-transform: skewX(45deg) translate(10px, -20px);
                transform: skewX(45deg) translate(10px, -20px);
    }

    .graph__cell {
        transition: .5s;
        cursor: pointer;
    }







