        -webkit-transform: skewY(-15deg) rotateX(15deg);
                transform: skewY(-15deg) rotateX(15deg);
    }
    
    .doughnut__doughnutProjectorDecorator {
        position: absolute;
        z-index: 0;
        top: calc(50% - 25px);
        left: calc(50% - 60px);
        width: 120px;
        height: 50px;
        background: linear-gradient(90deg, #9A1AFF 0%, #E864CB 100%);
        -webkit-transform: skewX(45deg);
                transform: skewX(45deg);
    }
    
    .doughnut__doughnutProjectorDecorator::before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        height: 100%;
        width: 20px;
        -webkit-transform: skewY(-45deg) translate(-20px, -10px);
