    .doughnut__proection {
        -webkit-animation: translation 2s ease-in-out 0s infinite alternate;
                animation: translation 2s ease-in-out 0s infinite alternate;
    }

    .doughnut__doughnutProjectorRays {
        position: absolute;
        width: 50px;
        top: -60px;
        left: calc(50% - 25px);
        display: flex;
        align-items: center;
        justify-content: center;
        -webkit-animation: translation 2s ease-in-out 0s infinite alternate;
                animation: translation 2s ease-in-out 0s infinite alternate;
    }

    .doughnut__doughnutProjectorRays_main {
        transition: .3s;
        -webkit-animation: opacityRaysThird 4s linear 0s infinite forwards;
                animation: opacityRaysThird 4s linear 0s infinite forwards;
    }

    .doughnut__doughnutProjectorRays_mainDelayed {
        transition: .3s;
        -webkit-animation: opacityRaysThird 6s linear 2s infinite forwards;
                animation: opacityRaysThird 6s linear 2s infinite forwards;
    }

    .doughnut__doughnutProjectorRays_first {
        transition: .3s;
        -webkit-animation: opacityRaysFirst 5s linear 0s infinite forwards;
                animation: opacityRaysFirst 5s linear 0s infinite forwards;
    }

    .doughnut__doughnutProjectorRays_second {
        transition: .3s;
        -webkit-animation: opacityRaysSecond 5s linear 0s infinite forwards;
                animation: opacityRaysSecond 5s linear 0s infinite forwards;
    }

    .doughnut__doughnutProjectorRays_third {
        transition: .3s;
        -webkit-animation: opacityRaysThird 5s linear 0s infinite forwards;
                animation: opacityRaysThird 5s linear 0s infinite forwards;
    }

    @-webkit-keyframes opacityRaysMain {
        0% {
            opacity: 1;
        }

        50% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }

    @keyframes opacityRaysMain {
