    margin-bottom: 20px;
    position: relative;
    z-index: 1;
    width: 100%;
    margin-left: 20px;
    height: 52px;
    background: linear-gradient(90deg, #9A1AFF 0%, #E864CB 100%);
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    transition: .3s;
}

.isometric__skillBox::before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100%;
    width: 20px;
    background:#9A1AFF;
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
    -webkit-transform: skewY(45deg) translate(-20px, 10px);
            transform: skewY(45deg) translate(-20px, 10px);
}

.isometric__skillBox::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 20px;
    width: 100%;
    background: linear-gradient(90deg, #9A1AFF 0%, #E864CB 100%);
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
    -webkit-transform: skewX(45deg) translate(10px, -20px);
            transform: skewX(45deg) translate(10px, -20px);
}

.isometric__skill {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}

.isometric__skillRatingWrapper {
    width: 150px;
    display: flex;
    justify-content: space-between;
}

.isometric__skillRating {
    position: relative;
    z-index: 1;
    width: 20px;
    height: 20px;
    background: #fff;
    font-size: 40px;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    /* box-shadow: -7px -7px 10px rgba(255, 255, 255, 0); */
    transition: .3s;
    /* transition: box-shadow .5s; */
}

.isometric__skillRating::before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100%;
    width: 5px;
    background:grey;
    -webkit-transform: skewY(45deg) translate(-5px, 2.5px);
            transform: skewY(45deg) translate(-5px, 2.5px);
}

.isometric__skillRating::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 5px;
    width: 100%;
    background:grey;
    -webkit-transform: skewX(45deg) translate(2.5px, -5px);
            transform: skewX(45deg) translate(2.5px, -5px);
}

.isometric__skillRating_active {
    background: #F0DB4F;
}

.isometric__skillRating_active::before, .isometric__skillRating_active::after {
    background: #F0DB4F;
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
}

.isometric__skillAnim {
    position: absolute;
    top: 18px;
    font-size: 18px;
    /* font-weight: bold; */
    opacity: 0;
    transition: .5s;
    height: 100%;
    line-height: 100%;
}

.isometric__skill:hover .isometric__skillAnim {
    opacity: 1;
    transition: .5s;
}

