    width: 3px;
    height: 8px;
    border-radius: 2px;
    background: white;
}

.mouse__arrow {
    font-size: 24px;
    padding-top: 10px;
    transition: .3s;
}

.navigationSection__mouse:hover .mouse__arrow {
    transition: .3s;
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
    -webkit-animation: mouseTranslation .5s ease 0s 1 forwards;
            animation: mouseTranslation .5s ease 0s 1 forwards;
}

.navigationSection__mouse:hover .mouse__wheel {
    transition: .3s;
    background: #F0DB4F;
}

.lift {
    margin: 20px 0;
}

.lift__floor {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 15px;
    margin: 20px 0;
    cursor: pointer;
}

.lift__floor_active .lift__box {
    background: #F0DB4F;
    transition: .3s;
}

.lift__floor_active .lift__box::before {
    background: #F0DB4F;
    transition: .3s;
}

.lift__floor_active .lift__box::after {
    background: #F0DB4F;
    transition: .3s;
}

.lift__floor_active .lift__name {
    color: #F0DB4F;
    transition: .3s;
}

.lift__floor_active .lift__gate {
    width: 0;
    background: #181818;
    transition: .5s;
}

.lift__box {
    position: relative;
    width: 20px;
    height: 15px;
    background: white;
    transition: .3s;
}

.lift__box::before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100%;
    width: 10px;
    background: rgba(255, 255, 255, .5);
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
    -webkit-transform: skewY(-45deg) translate(-10px, -5px);
            transform: skewY(-45deg) translate(-10px, -5px);
}

.lift__box::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    height: 10px;
    width: 100%;
    background: rgba(255, 255, 255, .5);
    -webkit-filter: brightness(.5);
            filter: brightness(.5);
    -webkit-transform: skewX(-45deg) translate(10px, 15px);
            transform: skewX(-45deg) translate(10px, 15px);
}

.lift__name {
    position: relative;
    -webkit-transform: translate(0px, 5px);
            transform: translate(0px, 5px);
    font-size: 24px;
    height: 36px;
    transition: .3s;
}

.lift__gate {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #181818;
    transition: .5s;
}

@-webkit-keyframes mouseTranslation {
    0% {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
    }

    33% {
        -webkit-transform: translate(0px, 2px);
                transform: translate(0px, 2px);
    }

    66% {
        -webkit-transform: translate(0px, -2px);
                transform: translate(0px, -2px);
    }

    100% {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
    }
}

@keyframes mouseTranslation {
    0% {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
    }

    33% {
        -webkit-transform: translate(0px, 2px);
                transform: translate(0px, 2px);
    }

    66% {
        -webkit-transform: translate(0px, -2px);
                transform: translate(0px, -2px);
    }

    100% {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
    }
}
